.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  width: 100%;
  height: 56px;

  font-weight: bold;
  font-size: 16px;
  line-height: 14px;
  text-decoration: none;

  border: 2px solid $color-blue;
  border-radius: 8px;

  transition: all 0.3s;
  cursor: pointer;

  &--primary {
    color: #fff;

    background-color: #005ecc;
    border-color: #005ecc;
    
  }

  &:visited {
    color: $color-blue;
  }

  &:hover {
    color: #fff;

    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
  }

  &:active {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 8px 14px rgba(0, 83, 181, 0.48);
  }
}
