.header {
  background-image: url(../images/header.jpg);
  background-position: center;
  background-size: cover;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    box-sizing: border-box;
    height: 100vh;
    padding: $header-top-padding 0 80px;
  }

  &__tel {
    text-decoration: none;

    @include onDesktop() {
      &::before {
        content: "+1 234 555-55-55";
  
        position: relative;
        top: 50%;
        transform: translate(-100%, -50%);
  
        display: block;
        width: max-content;
        padding-right: 20px;
  
        font-weight: 700;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #fff;

        opacity: 0;
        pointer-events: none;
      }

      &:hover::before {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &__title {
    font-size: 46px;
    line-height: 40px;
    font-weight: bold;
    color: #fff;
    letter-spacing: -1;

    &::before {
      content: "";
      display: block;
      width: 40px;
      height: 2px;
      margin-bottom: 24px;

      background-color: #fff;
    }
  }
}
